// 
const CustomPatch = {
  /**
   * 
   * @param method 
   * @param delay 
   * @param time 
   * @returns {Function}
   */
  throttle(method, delay, time) {
    let timeout, startTime = new Date();
    return function () {
      let context = this,
        args = arguments,
        curTime = new Date();
      clearTimeout(timeout);
      //  handler
      if (curTime - startTime >= time) {
        method.apply(context, args);
        startTime = curTime;
        // 
      } else {
        timeout = setTimeout(method, delay);
      }
    }
  }
};
export default CustomPatch;
