/**
 * axios
 * 
 */
import Vue from "vue";
import axios from "axios";
import router from "../router";
import store from "../store";
import Snotify from "vue-snotify";

const vm = new Vue();
/**
 * 
 * 
 */
const toLogin = () => {
  router.replace({
    path: "/login",
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
};

// axios
let instance = axios.create({timeout: 1000 * 12});
// post
instance.defaults.headers.post["Content-Type"] = "application/json";
/**
 * 
 * tokentoken
 */
instance.interceptors.request.use(
  config => {
    // token
    // tokentokentoken
    // token
    // 
    const token = localStorage.getItem("DLZEdToken");
    token && (config.headers.Authorization = token);
    return config;
  },
  error => Promise.error(error)
);

// 
instance.interceptors.response.use(
  // 
  res => (res.status === 200 ? Promise.resolve(res) : Promise.reject(res)),
  // 
  error => {
    console.log(error);
    const {response} = error;
    if (response) {
      // 2xx
      errorHandle(response.status, response.data.msg);
      return Promise.reject(response);
    } else {
      // 
      // eg:statenetwork
      // networkapp.vue
      // 
      store.commit("changeNetwork", false);
    }
  }
);
/**
 * 
 * @param {Number} status 
 * @param {String} errorMes 
 */
const errorHandle = (status, errorMes) => {
  // 
  switch (status) {
    case 400:
      vm.$snotify.error("400");

      break;
    // 401: token
    // token
    case 401:
      localStorage.removeItem("DLZEdToken");
      store.commit("changeLoginStatus", false);
      store.commit("changeHttpState", '');
      setTimeout(() => {
        // toLogin();
      }, 1000);
      break;
    // 403
    case 403:
      break;
    // 404
    case 404:
      console.log("");
      break;
    default:
      console.log(errorMes);
  }
};
export default instance;
