/**
 * order 
 */

// 
import base from "../base";
import axios from "../http"; // httpaxios

const order = {
  // 
  getOrderList(params) {
    return axios.get(`${base.produce}/Order`, { params });
  },
  // 
  getOrderDetails(params) {
    return axios.get(`${base.produce}/Order/${params}`);
  },
  // 
  createOrder(params) {
    return axios.post(`${base.produce}/Order`, JSON.stringify(params));
  }
};

export default order;
