/**
 * user 
 */

// 
import base from "../base";
import axios from "../http"; // httpaxios

const user = {
  /**
   * 
   * @returns {*}
   */
  getUserModel() {
    return axios.get(`${base.produce}/User/GetUserModel`);
  },
  setUserInfo(params){
    return axios.post(`${base.produce}/User/SetUserInfo`,JSON.stringify(params));
  }
};
export default user;
