<template>
    <div class="Learn">
        <!---->
        <div class="course-header">
            <div class="course-header-content">
                <div class="back-nav" @click="goBack">
                    <font-awesome-icon :icon="['fas','arrow-left']" class="back-icon"/>
                    <span></span>
                </div>
                <div class="courseName">
                    {{curriculumInfo.curriculumName}}
                </div>
                <!--<div class="courseTeacher">-->
                <!---->
                <!--</div>-->
            </div>
        </div>
        <!---->
        <div class="course">
            <!---->
            <div class="course-content">
                <!---->
                <div class="panel">
                    <div id="chaptersControl-panel">
                        <p></p>
                        <div class="chapter-list-container">
                            <div class="chapter-list" v-for="(chapter) in chapterList" :key="chapter.chapterId">
                                <div class="chapterName">{{chapter.chapterName}}</div>
                                <!---->
                                <ul class="section-list">
                                    <li class="section-item"
                                        v-for="item in chapter.subList"
                                        :class="{'sectionItem-active':sectionItemPosition == item.chapterId}"
                                        :key="item.chapterId"
                                        @click="chooseSection(item.chapterId)">
                                        <i></i>
                                        <span>{{item.chapterName}}</span>
                                    </li>
                                </ul>
                            </div>
                        </div>

                    </div>
                </div>
                <!---->
                <div class="course-video" v-show="chapterModel.videoFile">
                    <vue-plyr ref="plyr" class="plyr" :options="plyrOptions">
                        <video :src="chapterModel.videoFile">
                            <source :src="chapterModel.videoFile" type="video/mp4">
                        </video>
                    </vue-plyr>
                </div>
                <div class="course-article" v-show="chapterModel.passageContent">
                    <div class="course-article-content" v-html="chapterModel.passageContent"></div>

                </div>
            </div>
            <!---->
            <div class="other-content">
                <!---->
                <div class="qusAndAns">
                    <!---->
                    <div id="student" v-if="!isTeacher">
                        <div class="new-question">
                            <p class="new-question-header"></p>
                            <div class="new-question-container">
                                <img class="avatar-large" :src="userInfo.photo" :alt="userInfo.nickName">
                                <div class="column-box" style="align-items: flex-end;">
                                    <textarea placeholder="" id="new-question-content"
                                              v-model="newQuestionContent"></textarea>
                                    <button class="submitNewQuestion" @click="submitNewQuestion"
                                            :disabled="isNewQusBtnDisabled">
                                        
                                    </button>
                                </div>
                            </div>
                        </div>
                        <hr class="hr1">
                        <ul class="questions-list">
                            <li class="question-item" v-for="item in questionList">
                                <div class="question-userInfo">
                                    <img class="avatar-large" :src="item.photo" :alt="item.nickName">
                                    <div class="column-box" style="align-items: flex-start;justify-content: center;">
                                        <span class="question-user-name">{{item.nickName}}</span>
                                        <span class="question-user-other"></span>
                                    </div>
                                </div>
                                <div class="question-content">
                                    {{item.question}}
                                </div>
                                <div class="question-otherInfo">
                                    <!--<span class="question-time">{{item.createTime}}</span>-->
                                    <span class="question-chapter">{{item.chapterName}}</span>
                                </div>
                                <!--                -->
                                <div class="answer-container" v-if="item.answer">
                                    <div class="answer-userInfo">
                                        <img class="avatar-small" :src="item.teacherPhoto" :alt="item.teacherName">
                                        <span class="answer-user-name">{{item.teacherName}}</span>
                                    </div>
                                    <span class="answer-content">{{item.answer}}</span>
                                </div>
                                <!--                -->
                                <div class="answer-container" v-if="!item.answer">
                                    
                                </div>
                            </li>
                        </ul>
                    </div>
                    <!---->
                    <div id="teacher" v-if="isTeacher">
                        <ul class="questions-list">
                            <li class="question-item" v-for="item in questionList">
                                <!---->
                                <div class="question-userInfo">
                                    <img class="avatar-large" :src="item.photo" :alt="item.nickName">
                                    <div class="column-box" style="align-items: flex-start;justify-content: center;">
                                        <span class="question-user-name">{{item.nickName}}</span>
                                        <span class="question-user-other"></span>
                                    </div>
                                </div>
                                <!---->
                                <div class="question-content">{{item.question}}</div>
                                <div class="question-otherInfo">
                                    <!--<span class="question-time">{{item.createTime}}</span>-->
                                    <span class="question-chapter">{{item.chapterName}}</span>
                                </div>
                                <!---->
                                <div class="answer-container" v-if="item.answer">
                                    <!---->
                                    <div class="answer-userInfo">
                                        <img class="avatar-small" :src="item.teacherPhoto" :alt="item.teacherName">
                                        <span class="answer-user-name">{{item.teacherName}}</span>
                                    </div>
                                    <!---->
                                    <span class="answer-content">{{item.answer}}</span>
                                </div>
                                <!---->
                                <div class="answer-container-column" v-if="!item.answer">
                                    <textarea placeholder="" class="new-answer-content"
                                              v-model="newAnswerContent"></textarea>
                                    <button class="submitNewAnswer" @click="submitNewAnaswer(item.userQuestionId)"
                                            :disabled="isNewQusBtnDisabled">
                                        
                                    </button>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <el-row type="flex" justify="center" v-show="questionList!=''">
                        <el-pagination
                            @current-change="clickCallback"
                            :page-count="pageCount"
                            :current-page="currentPage"
                            layout="prev, pager, next, jumper"
                        >
                        </el-pagination>
                    </el-row>


                </div>
            </div>

        </div>
    </div>
</template>

<script>
import {SnotifyPosition, SnotifyToast, SnotifyToastConfig} from "vue-snotify";

export default {
    name: "Learn",
    props: ["curriculumId", "chapterId"],
    data() {
        return {
            // 
            userInfo: {},
            // 
            curriculumInfo: {},
            chapterList: [],
            // id
            _chapterId: "",
            // 
            questionList: [],
            // 
            newQuestionContent: "",
            // 
            isNewQusBtnDisabled: false,
            // 
            newAnswerContent: "",
            // 
            isNewAnsBtnDisabled: false,
            // 
            isTeacher: false,
            // 
            sectionItemPosition: 0,
            chapterModel: "",
            pageCount: 1,// 
            currentPage: 1,// 
            // 
            plyrOptions: {
                i18n: {
                    restart: "",
                    rewind: " {seektime}",
                    play: "",
                    pause: "",
                    fastForward: " {seektime}",
                    seek: "",
                    seekLabel: "{currentTime} of {duration}",
                    played: "",
                    buffered: "",
                    currentTime: "",
                    duration: "",
                    volume: "",
                    mute: "",
                    unmute: "",
                    enableCaptions: "",
                    disableCaptions: "",
                    download: "",
                    enterFullscreen: "",
                    exitFullscreen: "",
                    frameTitle: "Player for {title}",
                    captions: "",
                    settings: "",
                    menuBack: "",
                    speed: "",
                    normal: "",
                    quality: "",
                    loop: "",
                    start: "",
                    end: "",
                    all: "",
                    reset: "",
                    disabled: "",
                    enabled: "",
                    advertisement: "",
                    qualityBadge: {
                        2160: "4K",
                        1440: "HD",
                        1080: "HD",
                        720: "HD",
                        576: "SD",
                        480: "SD"
                    }
                }
            }
        };
    },
    computed: {
        player() {
            return this.$refs.plyr.player;
        }
    },
    components: {},
    methods: {
        /**
         * 
         * @param page {Number} 
         */
        clickCallback(page) {

            this.getQuestionList(page);
        },
        // 
        getCurriculumModel() {
            let data = {
                "Data.CurriculumId": this.curriculumId,
                Version: ""
            };
            this.$api.curriculum
                .getCurriculumModel(data)
                .then(res => {
                    this.curriculumInfo = res.data.data;
                    this.chapterList = res.data.data.chapterList;
                })
                .catch(error => {
                });
        },
        /**
         * 
         * @param pageNum {Number}
         */
        getQuestionList(pageNum) {
            let data = {
                "Condition.PageNum": pageNum,
                "Condition.PageSize": "",
                "Condition.CurriculumId": this.curriculumId,
                Version: ""
            };
            this.$api.curriculum.getQuestionList(data).then(res => {
                console.log(res);
                this.questionList = res.data.data.dataList;
                // 
                this.pageCount = res.data.data.page.totalCount;
            });
        },
        /**
         * 
         * @param chapterId {String} id
         */
        chooseSection(chapterId) {
            this._chapterId = chapterId;
            let data = {
                "Data.ChapterId": chapterId,
                Version: ""
            };
            this.$api.curriculum.getChapterModel(data).then(res => {

                this.sectionItemPosition = chapterId;
                this.chapterModel = res.data.data;
            });
        },
        // 
        submitNewQuestion() {
            this.isNewQusBtnDisabled = true;
            if (this.newQuestionContent === "") {
                this.$snotify.error("");
            } else {
                let data = {
                    data: {
                        chapterId: this._chapterId,
                        question: this.newQuestionContent
                    },
                    version: "string"
                };
                this.$api.curriculum
                    .submitQuestion(data)
                    .then(res => {

                        this.newQuestionContent = "";
                        this.isNewQusBtnDisabled = false;
                        this.$snotify.success("");
                        this.getQuestionList(1);
                    })
                    .catch(error => {
                        this.isNewQusBtnDisabled = false;
                        console.log(error);
                    });
            }
        },
        /**
         * 
         * @param questionId {String} id
         */
        submitNewAnaswer(questionId) {
            this.isNewAnsBtnDisabled = true;
            if (this.newAnswerContent === "") {
                this.$snotify.error("");
            } else {
                let data = {
                    data: {
                        questionId: questionId,
                        answer: this.newAnswerContent
                    },
                    version: "string"
                };
                this.$api.curriculum
                    .answerQuestion(data)
                    .then(res => {
                        this.isNewAnsBtnDisabled = false;
                        this.$snotify.success("");
                    })
                    .catch(error => {
                        this.isNewAnsBtnDisabled = false;
                        console.log(error);
                    });
            }
        },
        // 
        goBack() {
            this.$router.push({path: `/courseCenter`});
        },
        // 
        FisTeacher() {
            this.userInfo = JSON.parse(localStorage.getItem("DLZEdUserModel"));
            console.log(this.userInfo);
            for (let i = 0; i < this.userInfo.roles.length; i++) {
                if (this.userInfo.roles[i] === "Teacher") {
                    this.isTeacher = true;
                }
            }
        }
    },
    mounted() {
        this.getCurriculumModel();
        this.FisTeacher();
        this.chooseSection(this.chapterId);
        this.getQuestionList(1);
    }
};
</script>
<style scoped src="../common/css/Learn.css">
</style>
