<template>
  <div class="CourseDetails">
    <!---->
    <div class="course-header">
      <div class="course-header-content">
        <div class="toCourseList-nav" @click="goBack">
          <font-awesome-icon :icon="['fas','arrow-left']" class="back-icon" />
          <span></span>
        </div>
        <div class="courseName">
          {{curriculumInfo.curriculumName}}
        </div>
        <!--<div class="courseTeacher">-->
          <!---->
        <!--</div>-->
      </div>
    </div>
    <!---->
    <div class="container">
      <!---->
      <div class="left-container">
        <!---->
        <div class="course-toolbar">
          <div class="course-toolbar-item" :class="{toolbarActive:showDetails}" @click="showCurriculumDetail"></div>
          <div class="course-toolbar-item" :class="{toolbarActive:showChapters}" @click="showCurriculumChapters">
          </div>
        </div>
        <div class="courseIntroduction">
          <h4></h4>
          <span>
            {{curriculumInfo.introduction}}
          </span>
        </div>
        <!---->
        <div id="details" v-show="showDetails">
          <img src="../common/images/xqy.jpg" alt="">
        </div>
        <!---->
        <div id="chapters" v-show="showChapters">
          <div class="chapter" v-for="(chapter,index) in chapterList" :key="chapter.chapterId">
            <h4 class="chapter-name">{{index+1}} {{chapter.chapterName}}</h4>
            <span class="chapter-description">{{chapter.introduction === null?'':chapter.introduction}}</span>
            <ul class="section-list">
              <li class="section-item" v-for="(item,index) in chapter.subList" :key="item.chapterId">
                {{index+1}} {{item.chapterName}}
                <button class="section-button" @click="buyOrLearn(item.chapterId)">{{buyOrLearnText}}</button>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <!---->
      <div class="right-container">
        <div class="course-aside-info">
          <img class="course-aside-img" :src="curriculumInfo.image" alt="">
          <div class="course-aside-sell">
            <span class="course-aside-price">{{curriculumInfo.realPrice}}</span>
            <span class="course-aside-cost">{{curriculumInfo.price}}</span>
            <div class="addToCart" @click="addCart" :class="{'addToCart-added':isAddCar}">
              <span>{{carText}}</span>
              <div id="addToCart-icon-container">
                <font-awesome-icon :icon="['fas','cart-plus']" :class="{'addToCart-icon-added':isAddCar}"
                                   class="addToCart-icon" size="1x"/>
              </div>
            </div>
          </div>
          <div class="course-aside-item">
            <div class="icon-container">
              <font-awesome-icon :icon="['fas','key']" class="course-aside-item-icon" size="1x"/>
            </div>
            <span class="course-aside-item-title">:</span>
            <span class="course-aside-item-text"></span>
          </div>
          <button :to="buyOrLearnPath" id="buyOrLearn" @click="buyOrLearn(chapterList[0].subList[0].chapterId)">{{buyOrLearnText}}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { SnotifyPosition, SnotifyToast, SnotifyToastConfig } from "vue-snotify";
export default {
  name: "CourseDetails",
  props: ["curriculumId"],
  data() {
    return {
      showDetails: true,
      showChapters: false,
      curriculumInfo: {},
      chapterList: [],
      //
      isAddCar: false,
      carText: "",
      // ||
      buyOrLearnPath: "",
      buyOrLearnText: ""
    };
  },
  components: {},
  methods: {
    // 
    getCurriculumModel() {
      let data = {
        "Data.CurriculumId": this.curriculumId,
        Version: ""
      };
      this.$api.curriculum
        .getCurriculumModel(data)
        .then(res => {
          this.curriculumInfo = res.data.data;
          this.chapterList = res.data.data.chapterList;
          // 
          if (this.curriculumInfo.isFree === false) {
            if (this.curriculumInfo.isOwn === true) {
              this.buyOrLearnText = "";
            } else {
              this.buyOrLearnText = "";
            }
          } else {
            this.buyOrLearnText = "";
          }
        })
        .catch(error => {});
    },
    // 
    showCurriculumDetail() {
      this.showDetails = true;
      this.showChapters = false;
    },
    // 
    showCurriculumChapters() {
      this.showDetails = false;
      this.showChapters = true;
    },
    // 
    addCart() {
      if (this.isAddCar === true) {
        this.isAddCar = false;
        this.carText = "";
      } else {
        this.isAddCar = true;
        this.carText = "";
      }
    },
    /**
     * 
     * @param chapterId {String} id
     */
    buyOrLearn(chapterId) {
      if (this.$store.state.loginStatus === true) {
        if (this.curriculumInfo.isFree === false) {
          if (this.curriculumInfo.isOwn === true) {
            // idid
            this.$router.push({
              path: `/learn/${this.curriculumInfo.curriculumId}/${chapterId}`
            });
          } else {
            // 
            this.$router.push({
              path: `/confirmOrder/${this.curriculumInfo.curriculumId}`
            });
          }
        } else {
          // id
          this.$router.push({
            path: `/learn/${this.curriculumInfo.curriculumId}/${chapterId}`
          });
        }
      } else {
        this.$snotify.error("");
      }
    },
    // 
    goBack() {
      this.$router.back();
    }
  },
  mounted() {
    this.getCurriculumModel();
  }
};
</script>
<style scoped src="../common/css/CourseDetails.css">
</style>
