import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 
    httpState: String,
    // 
    network: Boolean,
    // 
    loginStatus: false,
    // 
    // 
    isShowRight:false,
    // 
    isShowAllRight:false,
    // 
    searchContent: "",
    // ,
    isUpdateUserIfo: false,

    // 
    // 
    showSortNav: false,
    //
    showCourseNav: false,
    // 
    showCertificate: false,
  },
  mutations: {
    changeHttpState(state, value) {
      state.httpState = value;
    },
    /**
     * 
     * @param state  {Object}  
     * @param value {Boolean} 
     */
    changeNetwork(state, value) {
      state.network = value;
    },

    // 
    /**
     *  
     * @param state {Object}  
     * @param value {Boolean} 
     */
    changeIsShowRight(state, value) {
      state.isShowRight = value;
    },
    /**
     *  
     * @param state {Object}  
     * @param value {Boolean} 
     */
    changeIsShowAllRight(state, value) {
      state.isShowAllRight = value;
    },

    /**
     *  
     * @param state {Object}  
     * @param value {Boolean} 
     */
    changeLoginStatus(state, value) {
      state.loginStatus = value;
    },
    /**
     * 
     * @param state
     * @param value
     */
    changeSearchContent(state, value) {
      state.searchContent = value;
    },
    /**
     * 
     * @param state
     * @param value
     */
    changeIsUpdateUserIfo(state, value) {
      state.isUpdateUserIfo = value;
    },
    /**
     * 
     * @param state
     * @param value
     */
    changeShowSortNav(state, value) {
      state.showSortNav = value;
    },
    /**
     * 
     * @param state
     * @param value
     */
    changeShowCourseNav(state, value) {
      state.showCourseNav = value;
    },
    /**
     * 
     * @param state
     * @param value
     */
    changeShowCertificate(state, value) {
      state.showCertificate = value;
    },
  },
  actions: {}
});
