import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import axios from "axios";
import VueAxios from "vue-axios";
import api from "./api/index"; // api
import VueAwesomeSwiper from "vue-awesome-swiper/src"; // 
import Snotify, {SnotifyPosition, SnotifyToast, SnotifyToastConfig} from "vue-snotify"; // 
import vRegion from "v-region"; //
import Element from 'element-ui'; // elementUI
import VueQriously from "vue-qriously"; // 
import VuePlyr from "vue-plyr"; // 
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome"; // FontAwesome
import {library} from "@fortawesome/fontawesome-svg-core"; //FontAwesome
import {
  faSearch,
  faRegistered,
  faUserCircle,
  faSchool,
  faHome,
  faMobile,
  faShieldAlt,
  faMars,
  faVenus,
  faPortrait,
  faEnvelope,
  faVenusMars,
  faTree,
  faMapMarkerAlt,
  faSuitcase,
  faKey,
  faTimes,
  faCartPlus,
  faArrowLeft,
  faAlignJustify
} from "@fortawesome/free-solid-svg-icons"; // 
import CustomPatch from "./common/js/CustomPatch"; //

library.add(
  faSearch,
  faRegistered,
  faUserCircle,
  faSchool,
  faHome,
  faMobile,
  faShieldAlt,
  faMars,
  faVenus,
  faPortrait,
  faEnvelope,
  faVenusMars,
  faTree,
  faMapMarkerAlt,
  faSuitcase,
  faKey,
  faTimes,
  faCartPlus,
  faArrowLeft,
  faAlignJustify
);

// 

// 
import "swiper/dist/css/swiper.css";
import "vue-snotify/styles/material.css";
import "vue-plyr/dist/vue-plyr.css";
import './common/css/element-#1CABA0/index.css';// elementUI

Vue.use(vRegion);
const snotifyOptions = {
  toast:{
    timeout: 2000,
    showProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    position: SnotifyPosition.rightTop
  }
};
Vue.use(Snotify, snotifyOptions);
Vue.use(VueAxios, axios);
Vue.use(VueAwesomeSwiper);
Vue.use(Element);

Vue.use(VueQriously);
Vue.use(VuePlyr);
Vue.config.productionTip = false;


Vue.component("font-awesome-icon", FontAwesomeIcon);

// 
router.beforeEach((to, from, next) => {
  // chrome
  document.body.scrollTop = 0;
  // firefox
  document.documentElement.scrollTop = 0;
  // safari  
  // window.pageYOffset = 0;
  // 
  store.commit("changeIsShowRight", false);
  store.commit("changeIsShowAllRight", false);
  next()
});


Vue.prototype.$api = api; // apivue
Vue.prototype.$CustomPatch = CustomPatch; // vue
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
