<template>
  <div>
    <!---->
    <div class="OrderCenter">
      <p class="title"></p>
      <!---->
      <transition name="fade-move-noLeave">
        <ul class="tableBar" v-show="isShowOrderList">
          <li class="tableBar-item " :class="{'tableBar-item-active':allTableBar}" @click="showAllOrder"></li>
          <li class="tableBar-item" :class="{'tableBar-item-active':completeTableBar}" @click="showCompleteOrder">
            
          </li>
          <li class="tableBar-item" :class="{'tableBar-item-active':noPayTableBar}" @click="showNoPayOrder"></li>

        </ul>
      </transition>
      <!---->
      <transition name="fade-move-noLeave">
        <div class="order-list" v-show="isShowOrderList">
          <!--          -->
          <transition name="order-item" mode="out-in">
            <div v-show="orderList==''" class="noOrders"></div>
          </transition>

          <transition-group name="order-item">
            <div class="order-item" v-for="item in orderList" :key="item.orderNumber">
              <img :src="item.thumbnailImage" :alt="item.curriculumName" class="order-img">
              <div class="order-content" @click="showOrderDetail(item.orderNumber)">
                <div class="order-header">
                  <span class="order-code">{{item.orderNumber}}</span>
                  <span class="order-date">{{item.createTime}}</span>
                </div>
                <div class="order-body">
                  <div class="order-body-item1">
                    <p class="order-name">{{item.curriculumName}}</p>
                    <p class="order-price"> {{item.realPrice}}</p>
                  </div>
                  <div class="order-body-item2">
                    <p class="order-finish">{{item.orderState.value}}</p>
                    <p class="order-pay"></p>
                  </div>
                </div>
                <div class="order-footer"></div>
              </div>
            </div>
          </transition-group>
          <!---->
          <el-row type="flex" justify="center">
            <el-pagination
              @current-change="clickCallback"
              :page-count="pageCount"
              :current-page="currentPage"
              layout="prev, pager, next, jumper"
            >
            </el-pagination>
          </el-row>

        </div>
      </transition>
      <!---->
      <transition name="fade-move-noLeave">
        <div class="back-nav" @click="backToOrderList" v-show="isShowOrderDetails">
          <font-awesome-icon :icon="['fas','arrow-left']" class="back-icon"/>
          <span></span>
        </div>
      </transition>
      <transition name="fade-move-noLeave">

        <div class="orderDetails" v-if="isShowOrderDetails">
          <div class="title-box">{{orderDetails.orderNumber}}</div>
          <div class="details-box">
            <img :src="orderDetails.thumbnailImage" :alt="orderDetails.curriculumName"
                 @click="toCourseDetails(orderDetails.curriculumId)"
                 class="order-details-img">
            <div class="order-details-content">
              <p class="order-details-name">{{orderDetails.curriculumName}}</p>
            </div>
            <p class="order-details-price">
              <span></span>
              <em></em>
              <span class="price-number">{{orderDetails.realPrice}}</span>
            </p>
            <p class="order-details-state">
              <span></span>
              <span class="state-noPay">{{orderDetails.orderState.value}}</span>
            </p>
          </div>
          <div v-show="orderDetails.orderState.key===0" class="pay-box">
            <h2 class="pay-title"></h2>
            <ul class="payType-list">
              <li class="payType" id="pay-wChart"></li>
            </ul>
          </div>
          <div v-show="orderDetails.orderState.key===0" id="pay-btn" @click="orderPay"></div>
        </div>
      </transition>
    </div>
    <!---->
    <div class="QRCode-bg" v-show="isShowPayQR">

      <div class="QRCode-container">
        <p class="QR-title"></p>
        <div class="QRCode-box">
          <!--          -->
          <qriously :value="QRCode" :size="200"/>
        </div>
        <p class="QR-tip"></p>
        <div class="button-box">
          <button class="QR-button-ok" @click="completePay"></button>
          <button class="QR-button-no" @click="closePay"></button>
        </div>
      </div>
    </div>
  </div>

</template>

<script>

export default {
  name: "OrderCenter",
  props: {},
  data() {
    return {
      isShowOrderList: true,// 
      allTableBar: true, // 
      completeTableBar: false,// 
      noPayTableBar: false,// 
      isShowOrderDetails: false,// 
      orderList: [],// 
      orderState: -1,// 
      orderDetails: {
        orderState: {
          key: null,
          value: ""
        },
      },// 
      pageCount: 1,// 
      currentPage: 1,// 
      isShowPayQR: false,// 
      QRCode: "",
      // 
      payInterval: Object,
    }
  },
  computed: {},
  watch: {},
  components: {},
  methods: {
    /**
     * 
     * @param condition Number  -101
     * @param currentPage Number 
     */
    getOrderList(condition, currentPage) {
      let data = {
        "Condition": condition,
        "PageOption.TotalCount": 0,
        "PageOption.CurrentPageIndex": currentPage,
        "PageOption.PageSize": 10,
        "SortOption.SortField": "",
        "SortOption.IsDesc": true,
        Version: ""
      };
      // console.log(data);
      this.$api.order.getOrderList(data).then(res => {
        this.orderList = res.data.data.dataList;
        this.pageCount = res.data.data.page.totalCount;
        this.currentPage = currentPage;
        // console.log(this.currentPage);
      });
    },
    /**
     * 
     * @param currentPage {Number} 
     */
    clickCallback(currentPage) {
      // console.log(currentPage);
      // console.log(this.currentPage);
      // this.currentPage = page;
      this.getOrderList(this.orderState, currentPage);

    },
    // 
    showAllOrder() {
      this.orderState = -1;
      this.getOrderList(-1, 1);
      this.allTableBar = true;
      this.completeTableBar = false;
      this.noPayTableBar = false;
    },
    // 
    showCompleteOrder() {
      this.orderState = 1;
      this.getOrderList(1, 1);
      this.allTableBar = false;
      this.completeTableBar = true;
      this.noPayTableBar = false;
    },
    // 
    showNoPayOrder() {
      this.orderState = 0;
      this.getOrderList(0, 1);
      this.allTableBar = false;
      this.completeTableBar = false;
      this.noPayTableBar = true;
    },
    /**
     * 
     * @param orderNumber {Number} 
     */
    showOrderDetail(orderNumber) {
      this.isShowOrderList = false;
      this.isShowOrderDetails = true;
      this.$api.order.getOrderDetails(orderNumber).then(res => {
        this.orderDetails = res.data.data;
      });
    },
    // 
    orderPay() {
      let data = {
        data: {
          orderNumber: this.orderDetails.orderNumber,
          paymentType: 2
        },
        version: "string"
      };
      this.$api.payment.webPay(data).then(res => {
        this.QRCode = res.data.data;
        // 
        this.isShowPayQR = true;
      });
      // 
      this.payInterval = setInterval(() => {

        this.$api.order.getOrderDetails(this.orderDetails.orderNumber).then(res => {
          console.log("");
          if (res.data.data.orderState.key === 1) {
            this.$snotify.success("");
            this.isShowPayQR = false;
            // 
            clearInterval(this.payInterval);

          }
          this.orderDetails = res.data.data;
        });
      }, 2000)
    },
    // 
    completePay() {
      this.$api.order.getOrderDetails(this.orderDetails.orderNumber).then(res => {
        if (res.data.data.orderState.key === 1) {
          this.$snotify.success("");
          this.isShowPayQR = false;
          // 
          clearInterval(this.payInterval);
        } else {
          this.$snotify.error("");
        }
      });
    },
    // 
    closePay() {
      this.isShowPayQR = false;
      // 
      clearInterval(this.payInterval);
    },
    /**
     * 
     * @param curriculumId ID
     */
    toCourseDetails(curriculumId) {
      this.$router.push({
        path: `/courseCenter/courseDetails/${curriculumId}`
      });
      this.toggle();
    },
    // 
    backToOrderList() {
      this.isShowOrderList = true;
      this.isShowOrderDetails = false;
      this.getOrderList(this.orderState, 1);
    },
  },
  mounted() {
    this.getOrderList(-1, 1);
  },
  destroyed() {
  }
}
</script>
<style scoped src="../common/css/OrderCenter.css">
</style>