<template>
  <div class="CRight">
    <transition name="fade-move">
      <div class="login-container" v-if="!isLogin">
        <!---->
        <div id="passwordLogin">
          <transition name="psw">
            <div v-show="isPassword">
              <div class="form-item">
                <div class="icon-container">
                  <font-awesome-icon :icon="['fas','mobile']" class="form-icon" size="2x"/>
                </div>
                <input type="tel" name="loginPhoneNumber" placeholder="" maxlength="11" class="form-input"
                       v-model="phoneNumber" @keyup.enter="login">
              </div>
              <div class="form-item">
                <div class="icon-container">
                  <font-awesome-icon :icon="['fas','key']" class="form-icon" size="2x"/>
                </div>
                <input type="password" autocomplete="new-password" placeholder="" class="form-input"
                       v-model="password" @keyup.enter="login"/>
              </div>
              <button class="form-button" @click="login" :disabled="isLoginBtnDisabled"></button>
            </div>
          </transition>
        </div>
        <button class="form-button" :id="switchCss" @click="switchLoginType">{{switchText}}</button>
        <div id="or">or</div>
        <!---->
        <div id="verifyLogin">
          <transition name="vry">
            <div v-show="isVerify">
              <div class="form-item">
                <div class="icon-container">
                  <font-awesome-icon :icon="['fas','mobile']" class="form-icon" size="2x"/>
                </div>
                <input type="tel" name="loginPhoneNumber" placeholder="" maxlength="11" class="form-input"
                       v-model="phoneNumber" @keyup.enter="getVerify">
              </div>
              <div class="form-item">
                <div class="icon-container">
                  <font-awesome-icon :icon="['fas','shield-alt']" class="form-icon" size="2x"/>
                </div>
                <input type="text" placeholder="" class="form-input verifyCode" v-model="verifyCode"
                       @keyup.enter="vLogin"/>
                <button class="form-button verifyButton" v-on:click="getVerify" v-bind:disabled="isBtnDisabled">
                  {{vBtnText}}
                </button>
              </div>
              <button class="form-button" @click="vLogin" :disabled="isLoginBtnDisabled"></button>
            </div>
          </transition>
        </div>
      </div>
    </transition>
    <transition name="fade-move">
      <div class="after-login-container" v-if="isLogin">
        <div id="content-box">
          <img :src="userInfo.photo" :alt="userInfo.nickName" class="avatar">
          <span class="nickName">
          {{userInfo.nickName}}
        </span>

          <button class="sidebar-btn-right" @click="showPersonal"
                  :class="{'sidebar-btn-right-active':isShowPersonal}">
          </button>
          <button class="sidebar-btn-right" @click="showSecurity"
                  :class="{'sidebar-btn-right-active':isShowSecurity}">
          </button>
          <button class="sidebar-btn-right"></button>
          <button class="sidebar-btn-right" @click="signOut"></button>
        </div>
        <!---->
        <div class="close-right" @click="toggle">
          <svg class="ham hamRotate ham7" :class="{active:mActive}" viewBox="0 0 100 100" width="50">
            <path
                class="line top"
                d="m 70,33 h -40 c 0,0 -6,1.368796 -6,8.5 0,7.131204 6,8.5013 6,8.5013 l 20,-0.0013">
            </path>
            <path
                class="line middle"
                d="m 70,50 h -40">
            </path>
            <path
                class="line bottom"
                d="m 69.575405,67.073826 h -40 c -5.592752,0 -6.873604,-9.348582 1.371031,-9.348582 8.244634,0 19.053564,21.797129 19.053564,12.274756 l 0,-40">
            </path>
          </svg>
          <span class="close-right-text"></span>
        </div>
      </div>
    </transition>
    <!--    -->
    <div class="cRight-content" v-if="isLogin&&isShowPersonal">
      <div id="personal">
        <p class="cRight-title"></p>
        <ul class="editPersonal-list">
          <!--          -->
          <li class="editPersonal-item-column">
            <div class="editPersonal-avatar">
              <img class="" :src="cacheUserInfo.photo" :alt="userInfo.nickName">
              <div class="editPersonal-avatar-hover" @click="isShowEditAvatar=true">
                
              </div>
            </div>
            <div class="edit-avatar" v-show="isShowEditAvatar">
              <p class="edit-avatar-title"></p>
              <ul class="avatar-list">
                <li class="avatar-item" v-for="(item,index) in editAvatarList" :class="{avatarSelected:item.isSelected}"
                    @click="chooseAvatar(index)">
                  <img :src="item.url" alt="">
                </li>

              </ul>
              <div class="avatar-button-box">
                <button class="avatar-button-submit" @click="submitAvatar"></button>
                <button class="avatar-button-cancel" @click="cancelAvatar"></button>
              </div>
            </div>
          </li>
          <li class="editPersonal-item">
            <label>
              <span class="editPersonal-item-title"> :</span>
              <input type="text" name="nickname" class="editPersonal-input" v-model="cacheUserInfo.nickName">
            </label>
          </li>
          <li class="editPersonal-item">
            <label>
              <span class="editPersonal-item-title"> :</span>
              <input type="text" name="phoneNumber" class="editPersonal-input" maxlength="11"
                     v-model="cacheUserInfo.phoneNumber" disabled>
            </label>
          </li>
          <!--          <li class="editPersonal-item">-->
          <!--            <label>-->
          <!--              <span class="editPersonal-item-title"> :</span>-->
          <!--              <input type="text" name="nickname" class="editPersonal-input" v-model="userInfo.nickName">-->
          <!--            </label>-->
          <!--          </li>-->
          <li class="editPersonal-item">
            <label>
              <span class="editPersonal-item-title"> :</span>
              <input type="text" name="idNumber" class="editPersonal-input" v-model="cacheUserInfo.idNumber"
                     maxlength="18">
            </label>
          </li>
          <li class="editPersonal-item">
            <label>
              <span class="editPersonal-item-title"> :</span>
              <input type="text" name="realName" class="editPersonal-input" v-model="cacheUserInfo.realName">
            </label>
          </li>
          <li class="editPersonal-item">
            <span class="editPersonal-item-title" style="flex-shrink: 0;"> :</span>

            <label id="woman">
              <input type="radio" class="form-radio" name="sex" value="0" v-model="cacheUserInfo.userSex">
            </label>
            <span class="icon-container-sex">
              <font-awesome-icon :icon="['fas','venus']" class="sex-icon woman" size="2x"/>
            </span>
            <label id="man">
              <input type="radio" class="form-radio" name="sex" value="1" v-model="cacheUserInfo.userSex">
            </label>
            <span class="icon-container-sex">
              <font-awesome-icon :icon="['fas','mars']" class="sex-icon man" size="2x"/>
            </span>
          </li>
          <!--          <li class="editPersonal-item">-->
          <!--            <label>-->
          <!--              <span class="editPersonal-item-title"> :</span>-->
          <!--              <input type="text" name="nickname" class="editPersonal-input" v-model="userInfo.nickName">-->
          <!--            </label>-->
          <!--          </li>-->
          <!--          <li class="editPersonal-item">-->
          <!--            <label>-->
          <!--              <span class="editPersonal-item-title"> :</span>-->
          <!--              <input type="text" name="nickname" class="editPersonal-input" v-model="userInfo.nickName">-->
          <!--            </label>-->
          <!--          </li>-->
          <li class="editPersonal-item">
            <span class="editPersonal-item-title" style="margin-right: 5rem;"> :</span>
            <ul class="domain-list">
              <li class="domain-item" v-for="(item,index) in userInfo.domainList"
                  :class="{domainChecked:item.isSelected}" :key="item.id" @click="item.isSelected=!item.isSelected">
                {{item.domainName}}
              </li>
            </ul>

          </li>
        </ul>
        <button class="personal-submit-btn" @click="setUserInfo"></button>
      </div>
    </div>
    <!--    -->
    <div class="cRight-content" v-show="isLogin&&isShowSecurity">
      <div id="security">
        <p class="cRight-title"></p>
        <ul class="editSecurity-list">
          <li class="editSecurity-item">
            <label>
              <span class="editSecurity-item-title"> : </span>
              <span class="editSecurity-item-text">******</span>
              <button class="editSecurity-btn" @click="isShowEditPassword=!isShowEditPassword"></button>
            </label>

            <div class="editSecurity-toggle" v-show="isShowEditPassword">
              <label>
                <span> :</span>
                <input type="password" autocomplete="new-password" placeholder="6" class="editSecurity-input"
                       v-model="newPassword">
              </label>
              <label>
                <span> :</span>
                <input type="text" placeholder="" class="editSecurity-input" v-model="editPasswordVerify">
                <button id="editSecurity-getVerify" @click="getPasswordVerify" v-bind:disabled="isBtnDisabled">
                  {{vBtnText}}
                </button>
              </label>
              <button class="personal-submit-btn" @click="submitNewPassword"></button>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "CRight",
    props: {
      mActive: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        editAvatarList: [
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar0.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar1.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar2.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar3.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar4.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar5.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar6.jpg",
            isSelected: false
          },
          {
            url: "https://api.dlzimageacademy.com/avatar/avatar7.jpg",
            isSelected: false
          }
        ],
        phoneNumber: "",
        password: "",
        verifyCode: "",
        isPassword: true,
        isVerify: false,
        vBtnText: "",
        isBtnDisabled: false,
        isLoginBtnDisabled: false,
        switchCss: "switchVry",// 
        switchText: "",
        userInfo: {},// 
        cacheUserInfo: {},// 
        avatar: '',// 
        isShowPersonal: false,// 
        isShowEditAvatar: false,// 
        isShowSecurity: false,// 
        isShowEditPassword: false,// 
        editPasswordVerify: '',//
        newPassword: ''//
      };
    },
    computed: {
      isLogin() {
        return this.$store.state.loginStatus;
      },
      isUpdateUserIfo() {
        return this.$store.state.isUpdateUserIfo;
      },
      // cacheUserInfo(){
      //   return JSON.parse(localStorage.getItem("DLZEdUserModel"));
      // }
    },
    watch: {
      /**
       * 
       */
      isLogin: function () {
        if (localStorage.getItem("DLZEdToken")) {
          this.userInfo = JSON.parse(localStorage.getItem("DLZEdUserModel"));
        }
      },
      /**
       * 
       */
      isUpdateUserIfo: function () {
        this.userInfo = JSON.parse(localStorage.getItem("DLZEdUserModel"));
      }
    },
    components: {},
    methods: {
      /**
       * 
       * @param index Number 
       */
      chooseAvatar(index) {
        for (let i = 0; i < this.editAvatarList.length; i++) {
          this.editAvatarList[i].isSelected = false;
        }
        this.editAvatarList[index].isSelected = true;
        this.cacheUserInfo.photo = this.editAvatarList[index].url;
      },
      // 
      submitAvatar() {
        let data = {
          data: this.cacheUserInfo,
          version: "string"
        };
        this.$api.user.setUserInfo(data)
          .then(res => {
            this.$snotify.success("");
            this.isShowEditAvatar = false;
            // userInfo
            localStorage.setItem("DLZEdUserModel", JSON.stringify(this.cacheUserInfo));
            this.$store.commit("changeIsUpdateUserIfo", !this.$store.state.isUpdateUserIfo);
          })
          .catch(error => {
            console.log(error);
          });
      },
      // 
      cancelAvatar() {
        this.isShowEditAvatar = false;
        this.cacheUserInfo.photo = this.userInfo.photo;
      },
      // 
      setUserInfo() {
        let data = {
          "data": this.cacheUserInfo,
          "version": "string"
        };
        this.$api.user.setUserInfo(data)
          .then(res => {
            this.$snotify.success("");
            this.isShowEditAvatar = false;
            // userInfo
            localStorage.setItem("DLZEdUserModel", JSON.stringify(this.cacheUserInfo));
            this.$store.commit("changeIsUpdateUserIfo", !this.$store.state.isUpdateUserIfo);
          })
          .catch(error => {
            console.log(error);
          });
      },
      //
      getVerify() {
        this.isBtnDisabled = true;
        // 
        let phoneReg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (this.phoneNumber === "") {
          this.isBtnDisabled = false;
          this.$snotify.error("", "");
        } else if (this.phoneNumber.length !== 11) {
          this.isBtnDisabled = false;
          this.$snotify.error("11", "");
        } else if (!phoneReg.test(this.phoneNumber)) {
          this.isBtnDisabled = false;
          this.$snotify.error("", "");
        } else {
          let verifyData = {
            "Data.PhoneNumber": this.phoneNumber,
            "Data.VerifyType": "VerifyCodeLogin"
          };
          this.$api.verify
            .getVerify(verifyData)
            .then(res => {
              // 
              let countdown = 60;
              let interval = setInterval(() => {
                this.vBtnText = countdown - 1 + "s";
                countdown--;
                // 1
                if (countdown <= 0) {
                  clearInterval(interval);
                  countdown = 60;
                  this.vBtnText = "";
                  this.isBtnDisabled = false;
                }
              }, 1000);
              this.$snotify.success("");
            })
            .catch(error => {
              this.$snotify.error(error.data.msg, "");
              this.isBtnDisabled = false;
            });
        }
      },
      // 
      login() {
        this.isLoginBtnDisabled = true;
        let phoneReg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (this.phoneNumber === "") {
          this.isLoginBtnDisabled = false;
          this.$snotify.error("", "");
        } else if (this.phoneNumber.length !== 11) {
          this.isLoginBtnDisabled = false;
          this.$snotify.error("11", "");
        } else if (!phoneReg.test(this.phoneNumber)) {
          this.isLoginBtnDisabled = false;
          this.$snotify.error("", "");
        } else {
          let loginData = {
            data: {
              account: this.phoneNumber,
              password: this.password
            },
            version: ""
          };
          // 
          this.$api.account
            .phoneLogin(loginData)
            .then(res => {
              // token
              localStorage.setItem("DLZEdToken", res.data.data);
              // 
              this.$api.user.getUserModel().then(res => {
                let userInfo = res.data.data;
                this.cacheUserInfo = userInfo;
                // userInfo
                localStorage.setItem("DLZEdUserModel", JSON.stringify(userInfo));
                //
                this.$emit("close");
                // vuex
                this.$store.commit("changeLoginStatus", true);
                this.isLoginBtnDisabled = false;
                // 
                if (this.$route.path.indexOf("/register") !== -1) {
                  this.$router.replace({path: `/`});
                }
              });
            })
            .catch(error => {
              this.isLoginBtnDisabled = false;
              this.$snotify.error(error.data.msg, "");
            });
        }
      },
      // 
      vLogin() {
        this.isLoginBtnDisabled = true;
        let phoneReg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (this.phoneNumber === "") {
          this.isLoginBtnDisabled = false;
          this.$snotify.error("", "");
        } else if (this.phoneNumber.length !== 11) {
          this.isLoginBtnDisabled = false;
          this.$snotify.error("11", "");
        } else if (!phoneReg.test(this.phoneNumber)) {
          this.isLoginBtnDisabled = false;
          this.$snotify.error("", "");
        } else {
          let loginData = {
            data: {
              phoneNumber: this.phoneNumber,
              verifyCode: this.verifyCode
            },
            version: ""
          };
          // 
          this.$api.account
            .verifyCodeLogin(loginData)
            .then(res => {
              // token
              localStorage.setItem("DLZEdToken", res.data.data);
              // 
              this.$api.user.getUserModel().then(res => {
                let userInfo = res.data.data;
                this.cacheUserInfo = userInfo;
                // userInfo
                localStorage.setItem(
                  "DLZEdUserModel",
                  JSON.stringify(userInfo)
                );
                //
                this.$emit("close");
                // vuex
                this.$store.commit("changeLoginStatus", true);
                this.isLoginBtnDisabled = false;
                // 
                if (this.$route.path.indexOf("/register") !== -1) {
                  this.$router.replace({path: `/`});
                }
              });
            })
            .catch(error => {
              this.isLoginBtnDisabled = false;
              this.$snotify.error(error.data.msg, "");
            });
        }
      },
      // 
      signOut() {
        localStorage.removeItem("DLZEdToken");
        localStorage.removeItem("DLZEdUserModel");
        this.toggle();
        // vuex
        this.$store.commit("changeLoginStatus", false);
      },
      // 
      getPasswordVerify() {
        this.isBtnDisabled = true;
        let verifyData = {
          "Data.PhoneNumber": this.userInfo.phoneNumber,
          "Data.VerifyType": "ResetPassword"
        };
        this.$api.verify
          .getVerify(verifyData)
          .then(res => {

            // 
            let countdown = 60;
            let interval = setInterval(() => {
              this.vBtnText = countdown - 1 + "s";
              countdown--;
              // 1
              if (countdown <= 0) {
                clearInterval(interval);
                countdown = 60;
                this.vBtnText = "";
                this.isBtnDisabled = false;
              }
            }, 1000);
            this.$snotify.success("");
          })
          .catch(error => {
            this.$snotify.error(error.data.msg, "");
            this.isBtnDisabled = false;
          });
      },
      // 
      submitNewPassword() {
        if (this.newPassword === '' || this.newPassword.length < 6) {
          this.$snotify.error("");
        } else if (this.editPasswordVerify === '') {
          this.$snotify.error("");
        } else {
          let data = {
            data: {
              account: this.userInfo.phoneNumber,
              password: this.newPassword,
              verifyCode: this.editPasswordVerify
            },
            version: "string"
          };
          this.$api.account.password(data)
            .then(res => {
              this.isShowEditPassword = !this.isShowEditPassword;
              this.$snotify.success(res.data.msg);
            })
            .catch(error => {
              console.log(error);
            })
        }

      },
      // toggle
      toggle() {
        this.$emit("toggle");
        this.$emit("closeAllShow");
      },
      // 
      showPersonal() {
        this.$emit("allShow");
        this.closeAllContent();
        this.isShowPersonal = true;
      },
      // 
      showSecurity() {
        this.$emit("allShow");
        this.closeAllContent();
        this.isShowSecurity = true;
      },
      // 
      switchLoginType() {
        this.isPassword = !this.isPassword;
        this.isVerify = !this.isVerify;
        if (this.isPassword) {
          this.switchCss = "switchVry";
          this.switchText = "";
        } else {
          this.switchCss = "switchPsw";
          this.switchText = "";
        }
      },
      // 
      closeAllContent() {
        this.isShowPersonal = false;
        this.isShowSecurity = false;
      }
    },
    mounted() {
      this.cacheUserInfo = JSON.parse(localStorage.getItem("DLZEdUserModel"));
    },
    destroyed(){
    }
  };
</script>
<style scoped src="../common/css/CRight.css">
</style>
