/**
 * account 
 */

// 
import base from "../base";
import axios from "../http"; // httpaxios

const account = {
  /**
   * 
   * @param params {Object} 
   * @returns {Q.Promise<any> | AxiosPromise<any>}
   */
  register(params) {
    return axios.post(`${base.produce}/Account/Register`, JSON.stringify(params));
  },

  /**
   * 
   * @param params {Object} 
   * @returns {Q.Promise<any> | AxiosPromise<any>}
   */
  phoneLogin(params) {
    return axios.post(`${base.produce}/Account/Login`, JSON.stringify(params));
  },
  /**
   * 
   * @param params
   * @returns {AxiosPromise<any>}
   */
  verifyCodeLogin(params) {
    return axios.post(`${base.produce}/Account/VerifyCodeLogin`, JSON.stringify(params));
  },
  /**
   * 
   * @param params
   * @returns {AxiosPromise<any>}
   */
  password(params){
    return axios.post(`${base.produce}/Account/Password`, JSON.stringify(params));
  }
};
export default account;
